import useLocales from '../../hooks/useLocales';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import { HOST_API } from '../../config';
import { fDescriptionTypeDate } from '../../utils/formatTime';

export default function BuyTicketBanner({ event, location }) {
    const { t } = useLocales();
    return (
        <div className="w-full flex flex-col sm:flex-row gap-8 items-center sm:items-start">
            <div className="rounded-xl aspect-5/3 overflow-hidden  w-96">
                <LazyLoadImage src={`${HOST_API}${event?.cover}`} alt="event-image" effect="blur" />
            </div>
            <div className='h-[222px] relative'>
                <div>
                    <h4 className="text-5xl mb-2 leading-[70px]">{event?.name}</h4>
                    <p className="leading-8">{fDescriptionTypeDate(event?.dateTime || event?.start)}</p>
                    <p className="leading-8 mb-4">{`${event?.place} ${location?.city.name}, ${location?.state.name}, ${location?.country.name}`}</p>
                </div>
                <button className="btn btn-primary btn-outline px-8 py-3.5 text-[19px] font-normal leading-6 capitalize absolute bottom-0 rounded-2xl h-auto border-2">{t('title.save_event')}</button>
            </div>
        </div>
    );
}
