import { useState } from 'react';
import useLocales from '../../hooks/useLocales';

import { fNumber } from '../../utils/formatNumber';

import { BlurModalBox } from '../../components/StyledComponents';
import GradientBorderWrapper from '../../components/wrappers/GradientBorderWrapper';

export default function DiscussPriceModal({ ticket, onClose, onSend }) {
    const { t } = useLocales();
    const [price, setPrice] = useState(1);
    const [content, setContent] = useState('');
    const onSubmit = () => {
        onSend({
            ticketId: ticket?._id,
            content,
            price
        });
    };

    return (
        <>
            <input type="checkbox" id="discuss-price-modal" className="modal-toggle" />
            <div className="modal">
                <BlurModalBox className="modal-box backdrop-blur-lg">
                    <div className="mb-2">
                        <label
                            htmlFor="discuss-price-modal"
                            className="btn btn-sm btn-circle absolute right-2 top-5 bg-transparent border-0"
                        >
                            ✕
                        </label>
                        <h3 className="font-bold text-lg">{t('title.modal.discuss_price')}</h3>
                    </div>
                    <hr className="-mx-5 border-stone-500" />

                    <div className="flex flex-col gap-4 p-2 mb-4">
                        <div className="flex justify-between gap-2">
                            <label className="text-stone-400">Original : {fNumber(ticket?.originPrice)}</label>
                            <label className="text-stone-400">Client Budget: {fNumber(ticket?.sellPrice)}</label>
                        </div>
                        <div className="flex justify-between gap-2 sm:items-center flex-col sm:flex-row items-start">
                            <span className="text-stone-400">Your Price : </span>
                            <div>
                                <GradientBorderWrapper>
                                    <div className="px-1 w-full flex">
                                        <input
                                            type="number"
                                            min={1}
                                            className="input text-right"
                                            value={price}
                                            onChange={(e) => setPrice(e.target.value)}
                                        />
                                    </div>
                                </GradientBorderWrapper>
                            </div>
                        </div>
                        <span className="text-stone-400 -mb-2">Description </span>
                        <GradientBorderWrapper>
                            <div className="p-2 w-full flex">
                                <textarea
                                    className="textarea w-full"
                                    rows={3}
                                    value={content}
                                    onChange={(e) => setContent(e.target.value)}
                                />
                            </div>
                        </GradientBorderWrapper>
                    </div>
                    <div className="flex justify-between gap-2 p-2">
                        <button
                            className="btn btn-primary px-8 capitalize btn-outline"
                            onClick={() => onClose('cancel')}
                        >
                            {t('action.cancel')}
                        </button>
                        <button className="btn btn-primary px-8 capitalize" disabled={!content || price < 1} onClick={onSubmit}>
                            {t('action.continue')}
                        </button>
                    </div>
                </BlurModalBox>
            </div>
        </>
    );
}
