import { Icon } from '@iconify/react';
import { useState } from 'react';

export default function CountChooser({ current = 0, max = 10, min = 0, size = "lg", onChanged, disabled }) {
    const [value, setValue] = useState(current);
    const onPlus = () => {
        if (max > value) {
            setValue(value + 1);
            onChanged(value + 1);
        }
    };
    const onMinus = () => {
        if (min < value) {
            setValue(value - 1);
            onChanged(value - 1);
        }
    };
    return (
        <div className="flex gap-1 items-center px-2">
            <button
                className={`btn btn-primary bg-primary/30 border-primary/30 hover:bg-primary/50 btn-circle btn-sm ${size === "sm" ? 'w-8 h-8' : 'w-12 h-12'}`}
                onClick={onMinus}
                disabled={disabled}
            >
                <Icon className={`text-primary text-${size === "sm" ? '' : '3'}xl`} icon="ic:round-minus" />
            </button>
            <span className={`w-12 text-[${size === "sm" ? '19' : '28'}px] text-center`}>{value}</span>
            <button
                className={`btn btn-primary bg-primary/30 border-primary/30 hover:bg-primary/50 btn-circle btn-sm ${size === "sm" ? 'w-8 h-8' : 'w-12 h-12'}`}
                onClick={onPlus}
                disabled={disabled}
            >
                <Icon className={`text-primary text-${size === "sm" ? '' : '3'}xl`} icon="ic:outline-plus" />
            </button>
        </div>
    );
}
