import { Icon } from '@iconify/react';
import { useState } from 'react';

export default function Accordion(props) {
    const [open, setOpen] = useState(false);
    const { detail, hideLine } = props;
    const handleOpen = () => {
        setOpen(!open);
    };
    return (
        <div className="flex flex-col w-full">
            <div className="flex justify-between gap-1 p-2 cursor-pointer" onClick={handleOpen}>
                <div className={`text-[19px] ${detail && "text-stone-400"}`}>{props.title}</div>
                <button className="text-primary btn btn-primary bg-primary/30 border-primary/30 hover:bg-primary/50 btn-circle btn-sm">
                    {(!open && <Icon icon="ic:baseline-keyboard-arrow-down" width={32} />) || (
                        <Icon icon="ic:baseline-keyboard-arrow-up" width={32} />
                    )}
                </button>
            </div>
            <div className={`flex flex-col overflow-hidden ${open ? 'h-full' : 'hidden'}`}>{props.children}</div>
            {!hideLine && <hr className={`border-t-1 border-stone-600 mt-${detail ? 2 : 6}`} />}
        </div>
    );
}
